*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu', monospace;
}


html{
  overflow: hidden !important;
}
.app{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  height: 647px;
  overflow: hidden !important;
  z-index: 9999;
}

.back-img{
  filter: brightness(50%) grayscale(60%);
  width: 100vw;
  height: 100vh;
  position: absolute;
  z-index: -1;
  object-fit: cover;
}

.input{
  position: absolute;
  bottom: 30px;
  left: 10px;
  border: none;
  border-radius: 32px;
  width: 250px;
  height: 40px !important;
  padding: 10px 30px 5px 10px;
  outline: none;
  color: #ddd;
  background-color: rgba(0, 0, 0, 0.3);
  font-size: 14px;
}

.text-holder{
  position: absolute;
  bottom: 75px;
  left: 10px;
  border-radius: 0 0 16px 16px;
  width: 250px;
  height: 200px;
  color: #ddd;
  overflow: auto ;  
  padding-bottom: 0px;
  display: flex;
  flex-wrap: wrap;
  background: linear-gradient(rgba(0,0,0,0),rgba(0,0,0,0.3));;
}
.text-holder::-webkit-scrollbar{
  visibility: hidden;
}
.cf{
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  padding: 5px 7px ;
  border-radius: 8px;
  color: white;
  background-color: #db6926;
}
.send{
  border-radius: 50%;
  padding: 6px;
  border: none;
  background-color: transparent;
  color: #ddd;
  position: absolute;
  bottom: 26px;
  left: 220px;
  transform: rotate(88deg);
  z-index: 3;
  outline: none;
}


.user-pic{
  width: 25px;
  height: 25px;
  background-color: #ccc;
  object-fit: cover;
  border-radius: 50%;
}
.text-buld{
  position: relative;
  display: flex;
  width: 100%;
  border-radius: 16px;
  padding-left: 5px;
  gap: 5px;
  margin-bottom: 5px;
}

.user-text{
  display: flex;
  flex-direction: column;
  padding: 5px;
  width: 200px;
}

.user-text span{
  font-size: 14px;
  width: max-content;
}
.user-text p{
  font-size: 12px;
  word-wrap: break-word !important;
  background-color: rgba(0, 0, 0, 0.4);
  padding: 4px;
  border-radius: 4px;
  width: 160px;
}

